import { Component, OnInit } from '@angular/core';
import { SelectFreeGroupBean } from './bean/selectFreeGroup.bean';
import { CusOperationHttpService } from './../../http/custom-operation/custom-operation.http.service';
import { ConfirmationService } from 'primeng/primeng';
import { Message } from 'primeng/primeng';
import { Router } from '@angular/router';
import { Commfunc } from '../../../../../@uisftech/common/service/commonFun';
import { CALENDAR_CN, tableMessage } from '../../../../../@uisftech/common/constant/config';
@Component({
  selector: 'app-group',
  templateUrl: './group.component.html',
  styleUrls: ['./group.component.scss'],
  // ,'../../../../../../assets/extend/table.css'
  providers: [CusOperationHttpService, Commfunc, ConfirmationService]
})
export class GroupComponent implements OnInit {
  //表格中无数据时显示的内容
  tabMesg: any = tableMessage;
  groupSearch: string = "客户分组查询";
  items: any;//接收后台数据
  selectFreeGroupBean: SelectFreeGroupBean = new SelectFreeGroupBean();
  //总页数
  total: number;
  totalNum: number;
  pageSize: number = 15;
  //模态框的标题
  headerTitle: string = "修改";
  //模态框是否显示
  display: boolean = false;
  addDisplay: boolean = false;
  //修改的参数
  updateValue: any = [];
  createDateEnd: any = '';
  createDateStasrt: any = '';
  ch: any = CALENDAR_CN;
  temData: Date = new Date();
  time: any;
  //传参   修改页面
  custom_arr: any = [];
  /**
  * 日期处理--
  */
  today: any = new Date();
  showFlag: boolean = true;
  groupStatus: any = [];
  code: any;
  //提示信息
  msgs: Message[] = [];

  //自选群搜索条件
  freeGroupId: string;
  freeGroupName: string;
  isManage: boolean = false;//创建人是否默认
  creatorId: any = { label: '', value: '' };
  createOrgId: any = { label: '', value: '' };  // 创建机构
  orgName: any = { label: '', value: '' };  // 所属机构
  ownerName: any = { label: '', value: '' };  // 群主
  
  //创建人模态框
  creatorDisplay: boolean = false;
  //创建机构模态框
  orgDisplay: boolean = false;
  //自选群
  mainCustNum: any;//自选群客户数
  mainGroupNum: any;//自选群个数

  ownerDisplay:boolean = false; // 群主选择模态框
  isN:any;

  constructor(
    public httpService: CusOperationHttpService,
    public startTime: Commfunc,
    public confirmationService: ConfirmationService,
    public router: Router,
    public commfunc: Commfunc
  ) {
    this.codeValues();
    this.groupStatus = this.code['GroupStatus'];
  }
  ngOnInit() {
    // this.items=[{freeGroupId:'1'}]
    this.selectFreeGroupBean.pageSize = 15;
    this.selectFreeGroupBean.pageNum = 1;
    this.query();//默认查询一次

    this.selectFreeGroupBasefInfo();

    this.creatorDefault();//
  }
  //码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }
  //查询
  query() {
    // debugger;
    this.selectFreeGroupBean.createDateStasrt = this.startTime.transDateN(this.createDateStasrt);
    this.selectFreeGroupBean.createDateEnd = this.startTime.transDateN(this.createDateEnd);

    this.selectFreeGroupBean.freeGroupId = this.freeGroupId;
    this.selectFreeGroupBean.freeGroupName = this.freeGroupName;
    this.selectFreeGroupBean.creatorId = this.creatorId.value;
    this.selectFreeGroupBean.createOrgId = this.createOrgId.value;
    this.selectFreeGroupBean.ownerName = this.ownerName.value;
    this.selectFreeGroupBean.orgName = this.orgName.value;

    let ob = this.httpService.selectFreeGroup(this.selectFreeGroupBean);
    ob.subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.items = data.freeGroupList;

        this.items.forEach((element, i) => {
          if (element.orgId && element.orgId != '') {
            this.queryPartyGroupListByPartyId(element.orgId, i);
          } else {
            this.items[i]['orgName'] = '';
          }
        });
        this.totalNum = data.totalNum;
        // this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message});
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }

    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
    })
  }

  creatorDefault() {
    let mesg = JSON.parse(this.commfunc.getSessionData('commonHeader'));
    let chName = JSON.parse(this.commfunc.getSessionDataCH('chName'));
    if (mesg.extParam.managePermission && mesg.extParam.managePermission == 'N') {
      this.isManage = true;
      this.creatorId = { label: chName.userName, value: mesg.userId };
    }
  }

  //创建人
  creatorClick() {
    this.creatorDisplay = true;
  }
   //选择群主的回调
  returnOwnerInfo(event) {
    // console.log(event);
    this.creatorId.value = event[0].tellerId;
    this.creatorId.label = event[0].tellerName;
    this.creatorDisplay = false;
    this.ownerName.value = event[0].tellerId;
    this.ownerName.label = event[0].tellerName;
  }

  //创建机构
  createOrgIdClick() {
    this.orgDisplay = true;
  }
  returnOrg(event) {
    // console.log(event);
    if (event.hasOwnProperty('orgId')) {
      this.createOrgId.value = event.orgId;
      this.createOrgId.label = event.orgName;
      this.orgName.value = event.orgId;
      this.orgName.label = event.orgName;
    }
    this.orgDisplay = false;
  }

  //自选群个数和客户数
  selectFreeGroupBasefInfo() {
    let mesg = JSON.parse(this.commfunc.getSessionData('commonHeader'));
    let ownerIdTemp = mesg['userId'];
    let params = {
      creatorId: ownerIdTemp
    };
    this.httpService.selectFreeGroupBasefInfo(params).subscribe(data => {
      if (data.returnCode.code == 'success') {
        console.log(data)
        this.mainGroupNum = data.mainGroupNum;
        this.mainCustNum = data.mainCustNum;
      } else {
        this.msgs = [];
        data.returnCode.code ? data.returnCode.code : '调用服务失败！';
        this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
      }
    }, err => {
      this.msgs = [];
      this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败！' }];
    })
  }

  //查找自选群高级查询页面
  toFreeQueryGroup() {
    this.router.navigate(['/pages/tzb/custom/custom-marketing/group/free-group-query'])
  }

  queryPartyGroupListByPartyId(orgId, i) {
    let params = {
      partyIdList: [orgId]
    }
    this.httpService.queryPartyGroupListByPartyId(params).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.items[i]['orgName'] = data.partyGroupList[0].groupName;
      } else {
        this.msgs = [];
        data.returnCode.code ? data.returnCode.code : '调用服务失败！';
        this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
      }
    }, err => {
      this.msgs = [];
      this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败！' }];
    })
  }
  //重置
  reset() {
    this.freeGroupId = '';
    this.freeGroupName = '';
    this.creatorId = { label: '', value: '' };
    this.createOrgId = { label: '', value: '' };
    this.orgName = { label: '', value: '' };
    this.ownerName = { label: '', value: '' };
    this.createDateStasrt = '';
    this.createDateEnd = '';
    this.query();
  }

  //分页
  paginate(event) {
    //每页显示的条数
    this.selectFreeGroupBean.pageSize = event.rows;
    //当前页
    let currentPage = event.page + 1;
    this.selectFreeGroupBean.pageNum = currentPage;
    //调用查询的方法
    this.query();
    this.selectFreeGroupBean.pageNum = 1;
  }
  //删除
  deleteClick(car) {
    this.confirmationService.confirm({
      message: '确认要删除吗?',
      accept: () => {
        // ;
        //条件
        let param = { freeGroupId: car.freeGroupId };
        this.httpService.deleteFreeGroup(param).subscribe(data => {
          if (data.returnCode.code == 'success') {
            this.msgs = [];
            this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            this.selectFreeGroupBean.pageNum = 1;
            this.query();
          } else {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
          }
        }, error => {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '提示', detail: "删除失败" });
        })
      }
    });
  }
  //修改按钮事件
  modifyClick(car) {
    console.log(car)
    this.headerTitle = '修改';
    this.display = true;
    this.updateValue = car;
    this.selectFreeGroupInfo(car.freeGroupId)
  }
  //修改的回调
  updateCall(param) {
    this.display = param;
    this.selectFreeGroupBean.pageNum = 1;
    this.query();
  }
  //日期
  showData() {
    this.showFlag = !this.showFlag;
    this.createDateEnd = null;
    this.temData = this.createDateStasrt;
  }
  //新增
  addBtn(event) {
    this.addDisplay = true;
    this.headerTitle = '创建自选群组';
  }
  addCall(param) {
    this.addDisplay = false;
    if (param) {
      this.msgs = param;
    }
    this.selectFreeGroupBean.pageNum = 1;
    this.query();
  }


  // 自选群信息查询
  selectFreeGroupInfo(freeGroupId) {
    let params = {
      freeGroupId: freeGroupId,
      pageNum: this.selectFreeGroupBean.pageNum,
      pageSize: this.selectFreeGroupBean.pageSize
    }

    let success_fnc = data => {
      data.custInfoList.forEach(item => {
        this.custom_arr.push(
          {
            custNo: item.custNo,
            custName: item.custName
          }
        )
      })
    }
    this.interfaceComFnc(this.httpService.selectFreeGroupDetailInfo(params), success_fnc)
  }
  //通用函数
  interfaceComFnc(service_method, success_fnc) {
    service_method.subscribe(data => {
      if (data.returnCode != null && data.returnCode.code == "success") {
        if (success_fnc !== null) {
          success_fnc(data);
        }
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
      }
    },
      error => {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
      });
  }

  jumpTo(item) {
    // id:items[i].freeGroupId,groupName:items[i].freeGroupName}
    let params = {
      groupId: item.freeGroupId,
      groupName: item.freeGroupName
    }
    this.router.navigate(['pages/tzb/custom/custom-group/custom-group-view', params])
  }
  //按钮权限
  permissionCheck(id) {
    return this.commfunc.permissionCheck(id)
  }
  toGroupView(col) {
    // let a = 'groupId=' + col.freeGroupId;
    // window.open('/#/pages/tzb/custom/custom-marketing/group/free-group-view;' + a);
    this.router.navigate(['pages/tzb/custom/custom-marketing/group/free-group-view'],{
    queryParams:{
      groupId:col.freeGroupId
    }
  });
  }

  // 选择群主
  ownerChoose() {
      this.creatorDisplay = true;
    }

    // 选择机构
    selectOrg() {
      this.orgDisplay = true;
      document.body.style.overflow = "hidden";
    }
  }