import { Component, OnInit, Output,Input, EventEmitter  } from '@angular/core';
import { Router, ActivatedRoute, ParamMap } from '@angular/router';
import { CusOperationHttpService } from './../../../http/custom-operation/custom-operation.http.service';
import { SelectFreeGroupBean } from '../bean/selectFreeGroup.bean';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { Message } from 'primeng/primeng';
import { tableMessage } from '../../../../../../@uisftech/common/constant/config';
import { Commfunc } from '../../../../../../@uisftech/common/service/commonFun';

@Component({
  selector: 'app-create-group',
  templateUrl: './create-group.component.html',
  styleUrls: ['./create-group.component.scss'],
  providers: [CusOperationHttpService]
})
export class CreateGroupComponent implements OnInit{
  //表格中无数据时显示的内容
  tabMesg: any = tableMessage;
  arr: any;
  cars: any;
  groupName:string;
  groupDescription:string;
  list: any=[{}];//接收后台数据
  selectFreeGroupBean: SelectFreeGroupBean = new SelectFreeGroupBean();
  userform: FormGroup; //校验

  //  is360 : boolean = false;
  //提示信息
  msgs: Message[] = [];
  @Output() outValue: EventEmitter<any> = new EventEmitter();
  @Input() custom_up_arr: any;
  @Input() is360:any;
   // @Input() inValue:any;
  constructor(
    public fb: FormBuilder,
    public httpService: CusOperationHttpService,
    public  router : Router,
    public  Commfunc : Commfunc
  ) {

  }

  ngOnInit() {
    this.userform = this.fb.group({
      'groupName': new FormControl('',[Validators.required,Validators.maxLength(60)]),
      'groupDescription': new FormControl('',[Validators.maxLength(1000)])
    });
    // this.list = [{ custNo: '11', custName: '2', idType: '9' }, { custNo: '12', custName: '2222', idType: '4' }, { custNo: '13', custName: '3', idType: '5' }]
  }
  // ngOnChanges(){
  //   // this.selectFreeGroupBean=new SelectFreeGroupBean();
  // }
  //点击保存
  save() { 
    //校验的
    for (const i in this.userform.controls) {
      this.userform.controls[i].markAsDirty();
    }
    //处理是否提交
    for (const i in this.userform.controls) {
      if (!this.userform.controls[i].valid) {
        return;
      }
    }
    let groupName = this.groupName;
    let groupDescription = this.groupDescription;
    let item = [];
    if(this.is360){
      this.custom_up_arr.forEach(items => {
        let cars = {};
        cars['custNo'] = items.custNo;
        cars['custName'] = items.custName;
        item.push(cars);
      });
    }
    let ch = JSON.parse(this.Commfunc.getSessionDataCH('chName'));
    let ch2 = JSON.parse(this.Commfunc.getSessionData('commonHeader'));
    console.log(ch);
    console.log(ch2);
    let custList = [{
      custName : ch['userName'],
      custNo : ch2['userId'],
    }]
    let param = { 
      freeGroupOrgId: ch2['orgId'], 
      freeGroupCreatorId: ch2['userId'], 
      freeGroupCreatorName: ch['userName'], 
      freeGroupName: groupName, 
      description: groupDescription 
    }
    this.httpService.createFreeGroup(param).subscribe(data => {
      if(data.returnCode.code=='success'){
        // this.groupName=undefined;
        // this.groupDescription=undefined;
        // this.selectFreeGroupBean=new SelectFreeGroupBean();
        //跳转到自选客户群
        
          // alert('自选群组创建成功') 
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '提示', detail: '自选群组创建成功' });
        this.router.navigate(['pages/tzb/custom/custom-marketing/group']);
        this.outValue.emit(this.msgs);
       
      
      }else{
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败" });
    })
    // this.selectFreeGroupBean=new SelectFreeGroupBean();
  }
  //取消
  cancel() {
    this.outValue.emit(false);
  }
  //分页
  paginate(event) {
    //每页显示的条数
    this.selectFreeGroupBean.pageSize = event.rows;
    //当前页
    let currentPage = event.page + 1;
    this.selectFreeGroupBean.pageNum = currentPage;
    //调用查询的方法
    // this.query();
    this.selectFreeGroupBean.pageNum = 1;
  }

  addCustom(){
    this.router.navigate(['pages/tzb/custom/custom-information/advanced-search/add-search']);
  }
}
