import { Component, OnInit, Input, Output, EventEmitter, OnChanges } from '@angular/core';
import { ActivatedRoute, Router } from "@angular/router";
import { CusOperationHttpService } from './../../../http/custom-operation/custom-operation.http.service';
import { SelectFreeGroupBean } from './../bean/selectFreeGroup.bean';
import { ConfirmationService } from 'primeng/primeng';
import { Message } from 'primeng/primeng';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import * as _ from 'lodash';
import { tableMessage } from '../../../../../../@uisftech/common/constant/config';
@Component({
  selector: 'app-update-group',
  templateUrl: './update-group.component.html',
  styleUrls: ['./update-group.component.scss'],
  providers: [CusOperationHttpService, ConfirmationService]
})
export class UpdateGroupComponent implements OnInit, OnChanges {
  updateGroup: string = "修改自选群组";
  //表格中无数据时显示的内容
  tabMesg: any = tableMessage;
  freeGroupId: string;
  freeGroupName: string;
  description: string;
  list: any;// 接收后台数据
  //总页数
  totalNum: number;
  pageSize: number = 10;
  pageNum: number = 1;
  freeGroup: string = '';
  descrip: string = '';
  total: number;
  userform: FormGroup; //校验
  //提示信息
  msgs: Message[] = [];
  selectFreeGroupBean: SelectFreeGroupBean = new SelectFreeGroupBean();
  @Input() inValue: any;
  @Output() outValue: EventEmitter<any> = new EventEmitter();
  @Input() custom_arr: any;
  custom_arr2: any = [];
  constructor(
    public httpService: CusOperationHttpService,
    public confirmationService: ConfirmationService,
    public fb: FormBuilder,
    public router: Router
  ) {
  }

  ngOnInit() {
    this.userform = this.fb.group({
      // 'freeGroupId': new FormControl('', Validators.required),
      'freeGroupName': new FormControl('', Validators.required),
      'description': new FormControl(''),
    });
    this.selectFreeGroupBean.pageSize = 10;
    this.selectFreeGroupBean.pageNum = 1;
    this.query();
  }

  ngOnChanges() {
    // for(let item in this.inValue){
    //   this.data[item] = this.inValue[item]
    // let param = { freeGroupId: this.inValue.freeGroupId,
    //   pageNum : this.pageNum,
    //   pageSize : this.pageSize};
    // this.httpService.selectFreeGroupDetailInfo(param).subscribe(data => {
    //   this.custom_arr = data.custInfoList;
    //   this.freeGroupName = data.freeGroupName;
    //   this.description = data.description;
    // })
  }

  query() {
   
    let param = {
      freeGroupId: this.inValue.freeGroupId,
      pageNum: this.pageNum,
      pageSize: this.pageSize
    };
    this.httpService.selectFreeGroupDetailInfo(param).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.custom_arr2 = [];
        data.custInfoList.forEach(item => {
          this.custom_arr2.push({
            custNo: item.custNo,
            custName: item.custName
          })
        })
        
        this.custom_arr2 = _.clone(this.custom_arr2)
        this.total = data.totalNum;
        this.freeGroupName = data.freeGroupName;
        this.descrip = data.description;
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
    })

  }
  //保存按钮
  save() {
    let param = { freeGroupId: this.inValue.freeGroupId, freeGroupName: this.inValue.freeGroupName, description: this.inValue.description }
    let ob = this.httpService.updateFreeGroupInfo(param);
    ob.subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.outValue.emit(false);
        this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
    })
  }
  //删除
  deleteClick(car) {
    let param = { custNo: car.custNo, freeGroupId: this.inValue.freeGroupId };
    this.httpService.deleteFreeGroupCustList(param).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.query();
        this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
    })
  }
  //取消
  modifyCloseBtn() {
    this.outValue.emit(false);
  }

  addCustom() {
    this.router.navigate(['pages/tzb/custom/custom-information/advanced-search/add-search', { freeGroupId: this.inValue.freeGroupId }]);
  }
  //delete
  delCustom(car) {
    let params = {
      custNo: car.custNo, freeGroupId: this.inValue.freeGroupId
    }
    let success_fnc = data => {
      this.query();
      this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
    }
    this.interfaceComFnc(this.httpService.deleteFreeGroupCustList(params), success_fnc)
  }
  //分页
  paginate(event) {
    //每页显示的条数
    this.pageSize = event.rows;
    //当前页
    this.pageNum = event.page + 1;
    //调用查询的方法
    this.query();
  }


  //通用函数
  interfaceComFnc(service_method, success_fnc) {
    service_method.subscribe(data => {
      if (data.returnCode != null && data.returnCode.code == "success") {
        if (success_fnc !== null) {
          success_fnc(data);
        }
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
      }
    },
      error => {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
      });
  }

}