import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';
//自选客户自选群组
import { GroupComponent } from './group.component';//自选客户自选群组
import { CreateGroupComponent } from './create-group/create-group.component';//创建自选群组
import { JoinGroupComponent } from './join-group/join-group.component';//加入自选群组
import { UpdateGroupComponent } from './update-group/update-group.component';//修改自选群组
export const routes: Routes = [

    {
        path: '',
        children: [
            { path: '', component: GroupComponent },
            { path: 'create-group', component: CreateGroupComponent },
            { path: 'update-group', component: UpdateGroupComponent },
            { path: 'join-group', component: JoinGroupComponent },
            { path: 'free-group-view', loadChildren: './free-group-view/free-group-view.module#FreeGroupViewModule'},
            { path: 'free-group-query', loadChildren: './free-group-query/free-group-query.module#FreeGroupQueryModule'},
        ]
    },


]

export const GroupRouting: ModuleWithProviders = RouterModule.forChild(routes);
