import { Component, OnInit } from '@angular/core';
import { CusOperationHttpService } from '../../../http/custom-operation/custom-operation.http.service';
import { Message } from 'primeng/primeng';
import { Router, ActivatedRoute } from '@angular/router';
import { Commfunc } from '../../../../../../@uisftech/common/service/commonFun';
@Component({
  selector: 'app-join-group',
  templateUrl: './join-group.component.html',
  styleUrls: ['./join-group.component.scss'],
  providers: [CusOperationHttpService],
})
export class JoinGroupComponent implements OnInit {
   joinGroup: string = "加入自选群组";
   groupName: string = '';
   params: any;
   custom_up_arr: any = [];
   custom_arr: any = [];
   pageSize: number = 10;
   pageNum: number = 1;
   totalNum: number;
   radioValue: any;

  //模态框的标题
   headerTitle: string;
  //模态框是否显示
   addDisplay: boolean = false;

   isMost: boolean = false;
   is360 : boolean = true;
  //提示信息
   msgs: Message[] = [];
   freeGroupId:string;
   freeGroupName:string;
   list: any = [
    {
      custNo: '10011',
      custName: 'dsjekj',
      id: '22'
    },
    {
      custNo: '100112',
      custName: 'dsjekj',
      id: 'ddd'
    }
  ];//接收后台数据
  constructor(
    public httpService: CusOperationHttpService,
    public route: ActivatedRoute,
    public router : Router,
    public commfunc: Commfunc,
  ) {
    console.log()
  }

  ngOnInit() {
    document.body.style.overflow='auto';
    //拿到参数 
    this.params = this.route.snapshot.params;
    console.log(this.route.snapshot.params['custNo'])
    let arr = JSON.parse(this.params.info);
    arr.forEach(item => {
      this.custom_up_arr.push({
        custNo: item.custNo == "undefined" ? '' : item.custNo,
        custName: item.custName == "undefined" ? '' : item.custName
      });
    });
    console.log(this.custom_up_arr)
    this.query();
  }
  query() {
    let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
    let oldManagePermission = commonHeader.extParam['managePermission'];
    commonHeader.extParam['managePermission'] = 'N';
    this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
    
    let parames = {
      createDateEnd:"",
      createDateStasrt:"",
      pageSize: this.pageSize,
      pageNum: this.pageNum,
      freeGroupId:this.freeGroupId,
      freeGroupName:this.freeGroupName

    }
    this.httpService.selectFreeGroup(parames).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.custom_arr = data.freeGroupList;
        this.totalNum = data.totalNum;
        // this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message});
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }

    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
    })

    let commonHeader1 = JSON.parse(this.commfunc.getSessionData('commonHeader'));
    commonHeader1.extParam['managePermission'] = oldManagePermission;
    this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader1));
  }
  //新增
  createGroup(event) {
    this.addDisplay = true;
    this.headerTitle = '新增';
  }
  addCall(param){
    this.addDisplay=param;
    this.pageNum=1;
    this.query();
  }
  confirm() {
    // debugger
    // let param = { freeGroupId: this.inValue };
    let groupName = this.groupName;
    let item = [];
    // this.custom_up_arr.forEach(items => {
    //   let cars = {};
    //   cars['custNo'] = items.custNo;
    //   cars['custName'] = items.custName;
    //   item.push(cars);
    // });
    if(typeof this.radioValue != 'number'){
      return
    }
    let param = { custInfoList: this.custom_up_arr, freeGroupId: this.custom_arr[this.radioValue].freeGroupId }
    let ob = this.httpService.addFreeGroupCustList(param);
    ob.subscribe(data => {
      if (data.returnCode.code == 'success') {
        // this.list = data;
        this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
        //跳转到自选群页面
        // this.router.navigate(['pages/tzb/custom/custom-marketing/group']);
        this.commfunc.setSessionData('custNo', this.custom_up_arr[0].custNo);
        this.router.navigate(['pages/tzb/custom/custom-marketing/group/free-group-view', { groupId: this.custom_arr[this.radioValue].freeGroupId }]);
        
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
    })
  }

    //分页
    paginate(event) {
      //每页显示的条数
      this.pageSize = event.rows;
      //当前页
      let currentPage = event.page + 1;
      this.pageNum = currentPage;
      //调用查询的方法
      this.query();
      this.pageNum = 1;
    }
}
